

.testimonial{
    // background-image: url(../../images/testimonial/testimonial_bg.jpg);
    background-image: url(../../assets/img/testimonial/testimonial_bg.jpg);
    background-position: 50% 50%;
    background-size: cover;
    padding:0!important;
}
.testimonial .overlay{
    background-color: rgba(0,0,0,.8);
}
.testimonial .section-header h3{
    color: #fff;
}
.testimonial .section-header p{
    color: #fff;
}
.testimonial .testimonial-item{
    text-align: center;
    color: #fff;
    max-width: 700px;
    margin: 0 auto;
}
.testimonial .testimonial-item .image{
    height: 100px;
    width: 100px;
    border: 4px solid #e1e1e1;
    border-radius: 100%;
    overflow: hidden;
    margin: 0 auto 20px;
}
.testimonial .testimonial-item h4{
    color: #fff;
    margin: 0px;
}
.testimonial .testimonial-item span{
    margin-bottom: 30px;
    display: inline-block;
}
.testimonial .testimonial-item p{
    color: #fff;
}
.testimonial .testimonial-pagination{
    text-align: center;
}
.testimonial .testimonial-pagination span{
    background-color: #00BFFF;
}